import React, { createContext, useContext, useReducer } from "react";
import { combineReducers } from "../services/combine-reducers";
import { IAction } from "./action";
import { appReducer } from "./app/reducers";
import { campaignsReducer } from "./campaigns/reducer";
import { IApplicationState, initialState } from "./state";

export const AppContext = createContext<{ state: IApplicationState, dispatch: React.Dispatch<IAction<any>> }>({ state: initialState, dispatch: () => null });

export const appReducers = {
    app: appReducer,
    campaigns: campaignsReducer,
};

interface AppStoreProps {
    children: React.ReactNode
}

export const AppStore: React.FC<AppStoreProps> = ({ children }) => {
    const [state, dispatch] = useReducer(combineReducers(appReducers), initialState);

    return (
        <AppContext.Provider value={{ state, dispatch }}>
            {children}
        </AppContext.Provider>
    );
};

export const useAppState = () => useContext(AppContext);